<template lang="">
    <article class="md:max-w-18rem">
        <figure class="pb-3">
            <img class="rounded-t-2xl" :src="item.img" :alt="item.pole" />
        </figure>
        <!-- <div
        v-match-heights="{el: ['p']}"
        class="px-5 py-3 border-2 border-dashed rounded-b-2xl border-blue"
        > -->
        <div        
        class="px-3 md:px-5 py-3 border-2 border-dashed rounded-b-2xl border-blue"        
        >
            <h3 class="text-xl font-black uppercase xs:text-3xl md:text-4xl text-blue font-DIN-Medium">{{ item.name }}</h3>
            <span class="text-3xl font-Clarissa text-blue">{{ item.country }}</span>

            <span class="block font-black uppercase font-DIN-Bold text-blue">desde</span>

            <div class="flex flex-row h-6 md:h-10 lg:h-12 justify-items-center">
                
                <!-- titulo o precio de la oferta -->                  
                  <div v-if="(item.name === 'Tramites')" class="relative flex justify-center flex-1">
                      <span
                          class="absolute z-10 block pt-4 text-3xl font-bold leading-3 price font-DIN-Bold text-blue xs:text-4xl md:text-5xl"
                      >${{ item.price }}&nbsp;OFF
                      
                          <span
                              class="absolute z-0 block pt-4 text-3xl font-bold leading-3 price -top-[2px] left-[3px] font-DIN-Bold text-orange xs:text-4xl md:text-5xl"
                              >${{ item.price }}&nbsp;OFF</span>                        
                      </span>                
                  </div>

                  <div v-else class="relative flex justify-center flex-1">
                      <span
                          class="absolute z-10 block pt-4 text-3xl font-bold leading-3 price font-DIN-Bold text-blue xs:text-4xl md:text-5xl"
                      >${{ item.price }}
                      
                          <span
                              class="absolute z-0 block pt-4 text-3xl font-bold leading-3 price -top-[2px] left-[3px] font-DIN-Bold text-orange xs:text-4xl md:text-5xl"
                              >${{ item.price }}</span>                        
                      </span>                
                  </div>            
                
            </div>

            <!-- Descripcion de la oferta -->
            <p class="px-1 md:px-4 py-3 pt-6 text-sm offer-description text-blue bg-blueGray lg:text-base" style="height: 150px">{{ item.description }}</p>

            <small class="flex">{{ item.info }}</small>
        </div>
        <button
        class="w-full my-5 text-sm btn-booking font-DIN-Bold md:text-2xl"
        @click="goOffer(item.keyword, {
            country: 'Cuba',
            product: item.name,
            product_name: item.description,
            price: item.price,
        })"
        >{{ item.btn }}</button>
    </article>
</template>
<script>
// import { emit } from 'process';

export default {
  name: "OfferComp",
  props: {
    item: {},
  },  
  methods: {
    goOffer(keyword, params) {
      this.$emit("offer", { keyword, params });
    },
  },
};
</script>
<style scoped>
article {
  width: 45%;
}
@media screen and (max-width: 520px) {
  p {
    height: 150px !important;
  }
}
</style>