<template>
    <div class="home-poles-list bg-blue">
        <h2 class="py-6 text-4xl md:text-6xl lg:text-4xl">- {{ title }} -</h2>

        <div class="flex flex-col justify-start gap-4 py-10 pl-10 sm:flex-row xs:pl-0 h-96 lg:h-auto">
          <div class="flex flex-row gap-6 sm:flex-col m-auto">
            <Splide class="prueba-slide" :options="slide_options">
              <SplideSlide v-for="(item, index) in poles" :key="index">
                <div class="flex flex-col items-start gap-6 p-3">
                  <figure
                    class="relative flex flex-col flex-1 gap-5 h-96 sm:m-auto w-70x100 sm:w-100x20 md:w-100x50 lg:w-100x70 xl:w-96"
                  ><!--  w-70x100  -->
                      <!-- :style="{filter: 'drop-shadow(11px 14px 7px #000)', width: '100%'}" -->
                    <img
                      :style="{width: '100%'}"
                      :src="item.img"
                      alt="polo1"
                    />
                    <figcaption
                      class="absolute top-0 left-0 w-full leading-10 xs:leading-none sm:rounded sm:text-6xl lg:text-10xl"
                    >{{ item.name }}</figcaption>
                  <button
                    class="px-8 py-1 mx-4 text-sm text-white uppercase btn-destinos font-DIN-Bold bg-orange rounded-75 md:px-16 md:mx-auto lg:text-2xl"
                    @click="goSinglePole(item.keyword, {
                      country: item.name + ' - ' + item.country,
                      pole: item.name,                      
                    })"
                  >descubre</button>
                  </figure>
                </div>
              </SplideSlide>
            </Splide>            
          </div>
        </div>
      </div>
</template>
<script>
import config from "@/config";
import { Splide, SplideSlide } from "@splidejs/vue-splide";
import "@splidejs/splide/dist/css/themes/splide-default.min.css";

const SLIDE_CONFIG = {
        direction   : "ltr",
        //gap         : '5rem',
        rewind      : true,
        wheel       : false,
        pagination  : false,
        autoplay    : false,
        height      : "auto",
        width       : "200rem",//100%
        fixedWidth  : "30rem",//22%
        fixedHeight : "35rem",
        breakpoints : {
          640: {
            // autoplay: false,
              wheel: true,
              height: "auto",
              width: "270px",
              fixedWidth: "30rem",
              fixedHeight: "22rem"
          }
        }
    }
export default {
    name: "PoleSlide",
    props: {
        title: {
          default: ''
        }
    },
    components: {
        Splide,
        SplideSlide,
    },
    data() {
        return {
            slide_options: SLIDE_CONFIG,
            poles: [
                {
                img: config.gallery_url + "general/destinos1.png",
                country: "Cuba",
                name: "Cuba",
                keyword: "cuba"
                },
                {
                img: config.gallery_url + "general/destinos2.png",
                country: "Mexico",
                name: "Cancun",
                keyword: "RQ"
                },
                {
                img: config.gallery_url + "general/destinos3.png",
                country: "Republica Dominicana",
                name: "Punta Cana",
                keyword: "RQ"
                }
            ],
        }
    },
    methods: {
      goSinglePole(keyword, params) {
        if (keyword == "RQ") {
          this.$emit('RQsinglePole',params);
        } else {
          this.$router.push({
            name: "SinglePole",
            params: { keyword }
          });
        }
      },
    }

}
</script>
<style scoped>
.home-poles-list figure figcaption {
  font-size: 3rem;
  font-family: Clarissa;
  top: calc(50% - 100px) !important;
  /* background: rgba(0,0 ,0 ,0.6 ); */
}
@media screen and (max-width: 640px) {
  .home-poles-list figure figcaption {
    font-size: 4rem;
    top: calc(70% - 100px) !important;
  }  
}    
</style>